
import footerNav from '@/components/footerNav/footerNav.vue';
import classNav from '@/components/classNav/classNav.vue';
import scroller from '@/components/scroller/scroller.vue';
import LoadMore from 'vux/src/components/load-more/index.vue'

import { SET_PAYMENT_OPTIONS } from '../../store/mutation-type.js';
import { getPhysicalOrderLists, cancelOrDelOrder } from '@/api/index.js';

export default {
name: 'Order',
data() {
return {
lists:[],
page: 1,
allowLoadMore: true,
listLoadedAll: false,
noLists: false,
emptyIcon:require('../../assets/k2.png'),

status: -1,
navs: [{
name: '全部',
status: -1,
}, {
name: '待支付',
status: 1,
}, {
name: '交易中',
status: 2,
}, {
name: '已完成',
status: 3,
}]
};
},
methods: {

handleToggleNavs(item) {
this.status = item.status;
this.lists = [];
this.page= 1;
this.allowLoadMore= true;
this.listLoadedAll= false;
this.noLists= false;
this.getLists();
},


getLists() {
if (!this.allowLoadMore || this.listLoadedAll || this.noLists) return;
this.allowLoadMore = false;
getPhysicalOrderLists({
user_id: this.$store.state.user.userid,
page:this.page,
status: this.status,
type: 2
}).then(res => {
if (res.data.data.length < 20) this.listLoadedAll = true;
if (res.data.data.length == 0 && this.lists.length == 0) this.noLists = true;
this.lists.push(...res.data.data);
this.allowLoadMore = true;
this.page++;
})
},


goOrderDetail(orderNo){
this.$router.push({
name:'OrderDetail',
params:{
orderNo:orderNo
}
})
},


goEvaluate(orderNo){
this.$router.push({
name:'Evaluate',
params:{
orderNo:orderNo
}
})
},


goPayment(orderNo, totalMoney){
this.$store.commit(SET_PAYMENT_OPTIONS, {
canUse: false,
orderNo: orderNo,
totalMoney: totalMoney,
orderType: 'OD',
kind: 2,
});
this.$router.push({
name:'Payment'
});
},


cancelOrDelOrder(orderNo,isDel){
let txt = isDel ? '删除' : '取消';
this.feedback.Confirm({
title: '',
msg: `确定要${txt}订单吗?`,
options: [{
txt: '取消',
color: '#999'
}, {
txt: '确定',
color: '#0bb20c',
callback: () => {
this.feedback.Loading.open(`${txt}中`);
cancelOrDelOrder({
user_id:this.$store.state.user.userid,
order_no: orderNo
}).then(res=>{
this.feedback.Loading.close();
this.feedback.Toast({
msg: res.data.info,
timeout: 1500
});
if(res.data.code == 1){
this.lists = [];
this.page= 1;
this.allowLoadMore= true;
this.listLoadedAll= false;
this.noLists= false;
this.getLists();
}
})
}
}]
});
}
},
mounted() {
this.getLists();
},
components: {
footerNav,
classNav,
scroller,
LoadMore
}
};




