export default {
  props: {
    navs: {
      require: true,
      type: Array
    }
  },
  data() {
    return {
      navsIndex: 0,
    }
  },
  methods:{
    handleToggleNavs(index){
      this.navsIndex = index;
      this.$emit('toggleNavs',this.navs[this.navsIndex]);
    }
  },
  mounted(){

  }
}




